<!-- Lynn -->
<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "scheduleStatusEdit-component",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      form: {},
      lectureTableColumns: [
        { id: "index", label: "項次", width: "75" },
        { id: "class_date", label: "上課日期" },
        { id: "status_Id", label: "目前課表狀態" },
      ],
    };
  },
  computed: {
    // 課表狀態資料 (All)
    lecture() {
      return this.$store.state.lecture;
    },
    // 課表狀態資料 (單頁)
    lectureList() {
      return this.$store.state.lecture_list;
    },
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },

    async saveList(item) {
      let obj = {
        schedule_No: item.schedule_No,
        status_Id: item.editSelect,
        class_Date: item.class_Date,
      };

      try {
        const res = await this.$store.dispatch("updScheduleStatus", obj);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: 5000,
            showClose: true,
            message: "儲存成功",
          });

          // 更新該筆課程的狀態，並將修改欄清空
          item.status_Id = item.editSelect;
          item.editSelect = "";
        }
      } catch (err) {
        console.log(err);
      }
    },

    changePageHandler(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "lecture",
        paramsListName: "lecture_list",
      });
    },
  },
  mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 將攜帶的資料，覆蓋於畫面上
    let data = JSON.parse(this.$route.query.data);
    this.form = data;
    // 更新表格之資料，以利分頁的顯示
    this.$store.commit("GET_STATUS_DATA", data);
  },
};
</script>

<template src="./scheduleStatusEdit.html"></template>
